@-webkit-keyframes easyoutelastic {
  0% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  9% {
    -webkit-transform: rotateX(210deg);
            transform: rotateX(210deg);
  }
  13% {
    -webkit-transform: rotateX(150deg);
            transform: rotateX(150deg);
  }
  16% {
    -webkit-transform: rotateX(200deg);
            transform: rotateX(200deg);
  }
  18% {
    -webkit-transform: rotateX(170deg);
            transform: rotateX(170deg);
  }
  20% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  60% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  80% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

@keyframes easyoutelastic {
  0% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  9% {
    -webkit-transform: rotateX(210deg);
            transform: rotateX(210deg);
  }
  13% {
    -webkit-transform: rotateX(150deg);
            transform: rotateX(150deg);
  }
  16% {
    -webkit-transform: rotateX(200deg);
            transform: rotateX(200deg);
  }
  18% {
    -webkit-transform: rotateX(170deg);
            transform: rotateX(170deg);
  }
  20% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  60% {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
  }
  80% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
  100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

@-webkit-keyframes rotatedrop {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  10% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  15% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  70% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  80% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

@keyframes rotatedrop {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  10% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  15% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  70% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  80% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.sidebar {
  min-width: 250px;
  max-width: 250px;
  background: #1c3643;
  color: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.sidebar.active {
  min-width: 80px;
  max-width: 80px;
  text-align: center;
}

.sidebar.active .sidebar-header h3,
.sidebar.active .CTAs {
  display: none;
}

.sidebar.active .sidebar-header strong {
  display: block;
}

.sidebar ul li a {
  text-align: left;
}

.sidebar.active ul li a {
  padding: 20px 10px;
  text-align: center;
  font-size: 0.85em;
}

.sidebar.active ul li a i {
  margin-right: 0;
  display: block;
  font-size: 1.8em;
  margin-bottom: 5px;
}

.sidebar.active ul ul a {
  padding: 10px !important;
}

.sidebar.active .dropdown-toggle::after {
  top: auto;
  bottom: 10px;
  right: 50%;
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
}

.sidebar .sidebar-header {
  padding: 20px;
  background: #24485a;
}

.sidebar .sidebar-header strong {
  display: none;
  font-size: 1.8em;
}

.sidebar ul.components {
  padding: 20px 0;
  border-bottom: 1px solid #47748b;
}

.sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  display: block;
}

.sidebar ul li a:hover {
  color: #24485a;
  background: #fff;
}
.sidebar ul li a i {
  margin-right: 10px;
}

.sidebar ul li.active > a,
a[aria-expanded="true"] {
  color: #fff;
  background: #24485a;
}

a[data-toggle="collapse"] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #24485a;
}

ul.CTAs {
  padding: 20px;
}

ul.CTAs a {
  text-align: center;
  font-size: 0.9em !important;
  display: block;
  border-radius: 5px;
  margin-bottom: 5px;
}

a.download {
  background: #fff;
  color: #24485a;
}

a.article,
a.article:hover {
  background: #24485a !important;
  color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
  .sidebar {
    min-width: 80px;
    max-width: 80px;
    text-align: center;
    margin-left: -80px !important;
  }
  .dropdown-toggle::after {
    top: auto;
    bottom: 10px;
    right: 50%;
    -webkit-transform: translateX(50%);
    transform: translateX(50%);
  }
  .sidebar.active {
    margin-left: 0 !important;
  }
  .sidebar .sidebar-header h3,
  .sidebar .CTAs {
    display: none;
  }
  .sidebar .sidebar-header strong {
    display: block;
  }
  .sidebar ul li a {
    padding: 20px 10px;
  }
  .sidebar ul li a span {
    font-size: 0.85em;
  }
  .sidebar ul li a i {
    margin-right: 0;
    display: block;
  }
  .sidebar ul ul a {
    padding: 10px !important;
  }
  .sidebar ul li a i {
    font-size: 1.3em;
  }
  .sidebar {
    margin-left: 0;
  }
  .sidebarCollapse span {
    display: none;
  }
}


